<template>
  <div>
    <div v-if="loading">
      <div class="loader"></div>
    </div>

    <div v-if="error">
      {{ error }}
    </div>

    <div v-if="data">
      <div>Comece a digitar o nome da disciplina</div>
      <el-autocomplete v-model="state" :fetch-suggestions="querySearch" placeholder="Comece a digitar o nome da disciplina" @select="searchTerm" :trigger-on-focus="false"></el-autocomplete>
      <div v-if="disciplina">
        <previsao-table :response-data=disciplina></previsao-table>
      </div>
    </div>
  </div>
</template>

<script>
  import './previsaoTable.js'
  import dataFetching from '../mixins/dataFetching'
  export default {
    name: 'previsao',
    mixins: [dataFetching],
    data() {
      return {
        term: null,
        data: null,
        disciplina: null,
        disciplinas: [],
        state: ''
      }
    },
    created() {
      const cb = () => {
        this.data = this.response.body
        for (const entry of this.data) {
          this.disciplinas.push(Object.values(entry)[1])
        }
        this.response = null
      }
      this.fetchData('lista_disciplinas', cb)
    },
    methods: {
      querySearch(queryStr, cb) {
        const results = queryStr ? _.filter(this.disciplinas, (o) => {
          return o.toLowerCase().includes(queryStr.toLowerCase())
        }) : this.disciplinas
        // o autocomplete só funciona se cada entry tiver a key 'value'!!
        const resultValues = results.map(value => ({ value }))
        cb(resultValues)
      },
      searchTerm(term) {
        const id = this.getDisciplinaIdByName(term.value)
        const cb = () => {
          this.disciplina = this.response.body
          this.response = null
        }
        this.fetchData(`previsao_disc/${id}`, cb)
      },
      getDisciplinaIdByName(name) {
        const entry = _.find(this.data, {'nome': name})
        if (entry) {
          return entry.disciplina_id
        }
      }
    }
  }
</script>

<style>
</style>



